.wp-block-media-manager-media-player {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: 0.5em;
  border-radius: 50vh;
}
.wp-block-media-manager-media-player .wp-block-media-manager-time-position-display {
  margin-top: 0;
  margin-right: 0.5em;
  margin-bottom: 0;
  margin-left: 0.2em;
}

.wp-media-manager-player-button {
  margin: 0 5px 0 0;
  border-radius: 9999px;
  line-height: 0;
}
.wp-media-manager-player-button button {
  padding: 0;
  border: 0;
}
.wp-media-manager-player-button button:focus {
  outline: none;
}
.wp-media-manager-player-button button svg {
  fill: currentColor;
}
.wp-media-manager-player-button button:not(:hover):not(:active):not(.has-text-color) {
  background-color: transparent;
  border-color: currentColor;
  color: inherit;
}

.is-small-size {
  max-height: 24px;
}
.is-small-size button {
  width: 24px;
  min-width: 24px;
  height: 24px;
  max-height: 24px;
}

